// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'onsenui/css/onsenui.css'// Onsen UI basic CSS
import 'onsenui/css/onsen-css-components.css'// Default Onsen UI CSS components

import Vue from 'vue'
import Vuex from 'vuex'
import VueOnsen from 'vue-onsenui'// For UMD
import VueResource from 'vue-resource'
import storeLike from './store'
import CustomToolbar from './partials/CustomToolbar.vue'
import AppNavigator from './AppNavigator.vue'

Vue.config.productionTip = false

Vue.use(Vuex)
Vue.use(VueOnsen)
Vue.use(VueResource)

// Register components globally
// Object.values(OnsenComponents).forEach(component => Vue.component(component.name, component)) // For ESM
Vue.component('custom-toolbar', CustomToolbar) // Common toolbar

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(AppNavigator),
  store: new Vuex.Store(storeLike),
  beforeCreate: function () {
    // Vue.prototype.baseUrl = ''
    Vue.prototype.baseUrl = 'http://218.58.65.20:8080/'
    this.$ons.platform.select('ios')
    // Shortcut for Material Design
    Vue.prototype.md = this.$ons.platform.isAndroid()
    // Set iPhoneX flag based on URL
    if (window.location.search.match(/iphonex/i)) {
      document.documentElement.setAttribute('onsflag-iphonex-portrait', '')
      document.documentElement.setAttribute('onsflag-iphonex-landscape', '')
    }
    // 模仿一下登陆
    // this.$http.post(this.baseUrl + 'wysqjw/api/userInfo.do', {
    //   jm: 'false',
    //   action: 'login',
    //   version: 999999999,
    //   logincode: 'gpqg',
    //   password: '111111'
    // }, {
    //   emulateJSON: true
    // }).then(response => {
    //   alert(response.data.msg)
    //   console.log(response.data)
    //   // get body data
    //   // this.someData = response.body;
    // }, response => {
    //   alert('error')
    //   console.log('error')
    // })
  }
})



